import * as React from 'react';
import style from '../style.module.css'
import MediaQuery from 'react-responsive';
import { Button, Card, CardContent, Container, Grid } from "@mui/material";
import { memo } from 'react';
import { useState } from "react";


const Shoplist=memo((shop)=> {

    const{list,page,shopamount}=shop
    console.log('list');
  const per = 10;


    return(
        <ul className={style.shoplist}>

<Grid container className={style.kkk}>
{list.slice(page,page+per).map((item,i)=>(
  
  // <Ppp>
  
  <Grid item xs={12} sm={6} key={i} >
<li key={i}> 

      {/* <img src={item.logo_image} alt="" /> */}
    {/* <Card> */}
    <div className={style.main}>

    <MediaQuery query='(min-width:961px)'>
<img src={item.photo.pc.l} alt="" />
    </MediaQuery>
<MediaQuery query='(max-width:960px)'>
<img src={item.photo.mobile.l} alt="" />
</MediaQuery>
<div className={style.url}>
<p>{item.name}</p>
<br /><br /><br />
<a href={item.urls.pc} target='branch'></a>
</div>

    </div>

    <div className={style.item}>
<p> <strong></strong> {item.address}</p>
<p><strong></strong>{item.access}</p>
<p><strong></strong>{item.free_drink}</p>
<p><strong></strong>{item.free_food}</p>
<p><strong>23</strong>{item.midnight}</p>
<p><strong></strong>{item.budget.average}</p>
<p><strong></strong>{item.open}</p>
<p><strong></strong>{item.parking}</p>
<p><strong></strong>{item.private_room}</p>
    </div>
 
  </li> 
  </Grid>

))}
</Grid>
</ul>
    )
});

export default Shoplist;