const ll=(state,action)=>{

    switch(action.type){
        case 'location1':
            return{
                ...state,
                contents:action.value
            }
        case 'location2':
            return{
                ...state,
                contents2:action.value
            }

            case 'location3':
                return{
                ...state,
                contents3:action.value
                }
            case 'location4':
                return{
                ...state,
                contents4:action.value
                }
                
            case 'location5':
                return{
                ...state,
                contents5:action.value
                }
                
            case 'location6':
                return{
                ...state,
                contents6:action.value
                }
                
            case 'location7':
                return{
                ...state,
                contents7:action.value
                }
                
            case 'location8':
                return{
                ...state,
                contents8:action.value
                }

            case 'reset':
                return{
                    ...state,
                    contents:null,
                    contents2:null,
                    contents3:null,
                    contents4:null,
                    contents5:null,
                    contents6:null,
                    contents7:null,
                    contents8:null,
                    
                }
            case 'data':
                return{
                    // ...state,
                    data:action.value
                }

    

            default: return state;

    }


}

