import * as React from 'react';
import { Button, Card, CardContent, Container, Grid, styled } from "@mui/material";
import MediaQuery from 'react-responsive';
import style from '../style.module.css'
import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import FormControl from '@mui/material/FormControl';
import NativeSelect from '@mui/material/NativeSelect';
import { memo } from 'react';

const Conditionbutton=memo((btn)=> {
  console.log('btn');

    const{condition1,condition2,condition3,condition4,condition5,conditionreset,val,change,click}=btn


    return(
        <Grid container>

<Grid item xs={12} sm={9} md={10} >
<div className={style.btn}>
<MediaQuery query='(max-width:600px)'>
<Button variant='contained' size='small' onClick={click} value={10}></Button>
<Button variant='contained' size='small' onClick={click} value={20}></Button>
<Button variant='contained' size='small' onClick={click} value={30}></Button>
<Button variant='contained' size='small' onClick={click} value={40}></Button>
<Button variant='contained' size='small' onClick={click} value={50}></Button>
<Button variant='contained' color='error' size='small' onClick={click} value={60}></Button>
</MediaQuery>
<MediaQuery query='(min-width:601px)'>
<Button variant='contained'  onClick={click} value={10}></Button>
<Button variant='contained' onClick={click} value={20}></Button>
<Button variant='contained' onClick={click} value={30}></Button>
<Button variant='contained' onClick={click} value={40}></Button>
<Button variant='contained' onClick={click} value={50}></Button>
<Button variant='contained' color='error' onClick={click} value={60}></Button>
</MediaQuery>
</div>

</Grid>

<Grid item xs={6} sm={3} md={2} >

<Box sx={{ minWidth: 120 }} className={style.select}>
      <FormControl fullWidth className={style.select2}>
        <InputLabel variant="standard" htmlFor="uncontrolled-native" color='success'>
  
        </InputLabel>
        <NativeSelect
          // defaultValue={0}
          value={val}
          inputProps={{
            name: 'age',
            id: 'uncontrolled-native',
          }}
          onChange={change}
          variant='filled'
          color='success'
          >
          <option value={0} ></option>
          <option value={10}>~1000</option>
          <option value={20}>~2000</option>
          <option value={30}>~3000</option>
          <option value={40}>~5000</option>
          <option value={50}>~10000</option>
          <option value={60}>~20000</option>
          <option value={70}>~30000</option>
        </NativeSelect>
      </FormControl>
    </Box>

    </Grid>

</Grid>
    )
    
})

export default Conditionbutton;